import React from 'react';
import PropTypes from 'prop-types';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { TransitionGroup } from 'react-transition-group';

import './History.css';

import SlideIn from '../Transitions/SlideIn';
import HistoryItem from './HistoryItem';

const History = ({ history, onRemove, setRegister, radix, clearHistory }) => {
  return (
    <div className="History">
      <div className="History__title">
        <h2>History</h2>
        <button onClick={clearHistory}>clear</button>
      </div>
      {
        history.size === 0
        ?
          <div className="History__empty">
            <p>History is empty. Do some operations :)</p>
          </div>
        : ''
      }
      <TransitionGroup
        component="ul"
        className="History__list">
        {
          history.slice(0, 10).map((item, i) => (
            <SlideIn key={item.guid}>
              <li className="History__list-item">
                <HistoryItem
                  {...item}
                  radix={radix}
                  onRemove={() => onRemove(i)}
                  setRegister={setRegister} />
              </li>
            </SlideIn>
          ))
        }
      </TransitionGroup>
    </div>
  );
};

History.propTypes = {
  history: ImmutablePropTypes.list.isRequired,
  onRemove: PropTypes.func.isRequired,
  setRegister: PropTypes.func.isRequired,
  radix: PropTypes.oneOf(['hex', 'dec', 'oct']),
  clearHistory: PropTypes.func.isRequired,
};
History.defaultProps = {
  radix: 'hex',
};

export default History;


