import React from 'react';
import PropTypes from 'prop-types';

import './Register.css';

import BitDisplay from '../BitDisplay';

const not = n => n ^ (0xFFFF);
const lshift = n => (n << 1) & 0xFFFF;
const rshift = n => n >> 1;

const Register = ({ value, onChange, radix }) => {
  return (
    <div className="Register">
      <div className="Register__operations">
        <button onClick={() => onChange(0)}>clear</button>
        <button onClick={() => onChange(not(value))}>not</button>
        <button onClick={() => onChange(lshift(value))}>{'<<'}</button>
        <button onClick={() => onChange(rshift(value))}>{'>>'}</button>
      </div>
      <BitDisplay radix={radix} value={value} onChange={onChange} />
    </div>
  );
}

Register.propTypes = {
  value: PropTypes.number.isRequired,
  onChange: PropTypes.func.isRequired,
  radix: PropTypes.oneOf(['hex', 'dec', 'oct']),
};
Register.defaultProps = {
  radix: 'hex',
};

export default Register;


