import React from 'react';
import PropTypes from 'prop-types';

import './NumShower.css';

const numToString = (n, type) => {
  switch (type) {
    case 'hex': return n.toString(16).toUpperCase();
    case 'oct': return n.toString(8);
    case 'dec':
    default: return n.toString();
  }
}

const typeToString = (radix) => {
  switch (radix) {
    case 'hex': return '0x';
    case 'oct': return '0o';
    case 'dec':
    default: return '';
  }
}

export const NumShower = ({ value, radix }) => (
  <span className="NumShower">
    <span className="NumShower__radix">{typeToString(radix)}</span>
    <span className="NumShower__value">{numToString(value, radix)}</span>
  </span>
);

NumShower.propTypes = {
  value: PropTypes.number.isRequired,
  radix: PropTypes.oneOf(['hex', 'dec', 'oct']),
};
NumShower.defaultProps = {
  type: 'hex',
};

export default NumShower;


