import React from 'react';
import { CSSTransition } from 'react-transition-group';

import './SlideIn.css';

const SlideIn = ({ children, ...props }) => (
  <CSSTransition
    {...props}
    timeout={300}
    classNames="SlideIn">
    {children}
  </CSSTransition>
);

export default SlideIn;


