import { Map } from 'immutable';

import { GLOBALS_SETRADIX } from '../actions/globals';

export const initialState = Map({
  radix: 'hex',
});

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case GLOBALS_SETRADIX:
      return state.set('radix', action.radix);
    default:
      return state;
  }
};

export default reducer;



