import React from 'react';
import PropTypes from 'prop-types';

import './Bit.css';

const Bit = ({ on, onClick }) => {
  const state = `Bit--${ on ? 'one' : 'zero'}`;
  return (
    <div className={`Bit ${state}`} onClick={onClick}>
      <div className="Bit__container">
        <div className="Bit__zero">0</div>
        <div className="Bit__one">1</div>
      </div>
    </div>
  );
}

Bit.propTypes = {
  on: PropTypes.bool,
  onClick: PropTypes.func,
};
Bit.defaultProps = {
  on: true,
};

export default Bit;


