import { Map } from 'immutable';

import { REGISTERS_SET } from '../actions/registers';

export const initialState = Map({
  A: 0,
  B: 0,
});

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case REGISTERS_SET:
      return state.set(action.register, action.value);
    default:
      return state;
  }
};

export default reducer;


