import React from 'react';
import PropTypes from 'prop-types';
import ImmutablePropTypes from 'react-immutable-proptypes';

import './Registers.css';

import Register from './Register';

const Registers = ({ registers, onChange, radix }) => {
  const A = registers.get('A');
  const B = registers.get('B');
  
  return (
    <div className="Registers">
      <div className="Registers__register Registers__register--a">
        <h2 className="Registers__name">A</h2>        
        <Register radix={radix} value={A} onChange={n => onChange('A', n)}/>
      </div>
      <div className="Registers__register Registers__register--b">
        <h2 className="Registers__name">B</h2>
        <Register radix={radix} value={B} onChange={n => onChange('B', n)}/>
      </div>
    </div>
  );
}

Registers.propTypes = {
  registers: ImmutablePropTypes.mapOf(
    PropTypes.number,
  ),
  radix: PropTypes.oneOf(['hex', 'dec', 'oct']),
  onChange: PropTypes.func.isRequired,
};
Register.defaultProps = {
  radix: 'hex',
};

export default Registers;


