import React from 'react';
import PropTypes from 'prop-types';

import { range, reverse } from 'lodash';

import './BitDisplay.css';

import Bit from './Bit';
import NumShower from './NumShower';

/**
 * lsbArr returns a bool array of lenght 16
 * containing the values of the 16 least
 * significant bits of the input number.
 * Array is little endian
 * @param {number} n 
 */
const lsbArr = (n) => range(16)
    .fill(0)
    .map((_, i) => (n & (1<<i)) !== 0);

const BitDisplay = ({ value, onChange, radix }) => {
  // value is javascript number.
  // we're only interested in the 16
  // least signifant bits.

  const bitClick = (n) => {
    if (!onChange) return;
    const mask = 1 << n;
    const toggled = value ^ mask;
    onChange(toggled);
  }

  return (
    <div className="BitDisplay">
      <div className="BitDisplay__bits">
        {
          reverse(
            lsbArr(value)
              .map((on, i) => <Bit key={i} on={on} onClick={() => bitClick(i)} />)
          )
        }
      </div>
      <div className="BitDisplay__value">
        <NumShower value={value} radix={radix} />
      </div>
    </div>
  );
};

BitDisplay.propTypes = {
  showValue: PropTypes.bool,
  value: PropTypes.number.isRequired,
  onChange: PropTypes.func,
  radix: PropTypes.oneOf(['hex', 'dec', 'oct']),
};
BitDisplay.defaultProps = {
  showValue: true,
  radix: 'hex',
};


export default BitDisplay;


