import React from 'react';
import PropTypes from 'prop-types';

import './HistoryItem.css';

import BitDisplay, { NumShower } from '../BitDisplay';

const opToSymbol = (op) => {
  switch (op) {
    case 'and': return '&';
    case 'or': return '|';
    case 'xor': return '^';
    default: return 'unknown op';
  }
}

const HistoryItem = ({ res, op, A, B, onRemove, setRegister, radix }) => {
  return (
    <div className="HistoryItem">
      <div className="HistoryItem__actions">
        <button onClick={onRemove}>&times;</button>
        <button onClick={() => setRegister('A', res)}>A</button>
        <button onClick={() => setRegister('B', res)}>B</button>
      </div>
      <BitDisplay radix={radix} value={res} />
      <div className="HistoryItem__source">
        <NumShower radix={radix} value={A} />
        { ` ${opToSymbol(op)} `}
        <NumShower radix={radix} value={B} />        
      </div>
    </div>
  );
};

HistoryItem.propTypes = {
  res: PropTypes.number.isRequired,
  A: PropTypes.number.isRequired,
  B: PropTypes.number.isRequired,
  op: PropTypes.oneOf(['and', 'or', 'xor']).isRequired,
  onRemove: PropTypes.func.isRequired,
  setRegister: PropTypes.func.isRequired,
  radix: PropTypes.oneOf(['hex', 'dec', 'oct']),
};
HistoryItem.defaultProps = {
  radix: 'hex',
};

export default HistoryItem;


