export const HISTORY_ADD = 'HISTORY_ADD';
export const HISTORY_REMOVE = 'HISTORY_REMOVE';
export const HISTORY_CLEAR = 'HISTORY_CLEAR';

export const add = item => ({
  type: HISTORY_ADD,
  item,
});

export const remove = index => ({
  type: HISTORY_REMOVE,
  index,
});

export const clear = () => ({
  type: HISTORY_CLEAR,
});


