import React from 'react';
import PropTypes from 'prop-types';

import { radices } from '../actions/globals';

const Globals = ({ radix, setRadix }) => {
  return (
    <div className="Globals">
      {
        radices.map((_radix) => (
          <button
            key={_radix}
            disabled={radix === _radix}
            onClick={() => setRadix(_radix)}>
            {_radix}
          </button>
        ))
      }
      
    </div>
  );
};

Globals.propTypes = {
  radix: PropTypes.oneOf(radices).isRequired,
  setRadix: PropTypes.func.isRequired,
};

export default Globals;


